@charset "UTF-8";
.host-location {
  font-size: 14px;
  line-height: 16px; }

.descriptive_text, .details_name, .contact-host, .amenities_item, .booking_price, .apartment_type, .host-description {
  font-size: 16px;
  line-height: 19px; }

.details_date, .apartment-amenities > h2 {
  font-size: 20px;
  line-height: 23px; }

h2, .review_hosted_font {
  font-size: 28px;
  line-height: 33px; }

.booking_input, .booking_button {
  width: 270px;
  height: 50px;
  background: white;
  border-radius: 3px;
  font-size: 16px;
  line-height: 19px; }

.footer-column-title, .footer-column-item-title {
  text-decoration: none;
  color: #3d4e61; }

.main-container, .booking-info-block {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: Avenir;
  /* Имя шрифта */
  src: url(../fonts/Avenir/Avenir-Book.ttf);
  /* Путь к файлу со шрифтом */ }

