@font-face {
  font-family: Avenir;
  /* Имя шрифта */
  src: url(../fonts/Avenir/Avenir-Book.ttf);
  /* Путь к файлу со шрифтом */ }

body {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  font-family: Avenir, sans-serif;
  color: #3d4e61; }

h1 {
  font-size: 36px;
  line-height: 42px;
  color: #3d4e61;
  margin-top: 0; }

h2 {
  color: #3d4e61; }

a {
  text-decoration: none;
  color: #3d4e61; }

.is-bold {
  font-weight: bold; }

.descriptive_text {
  width: 491px;
  height: 120px;
  color: #3d4e61; }

.header-container {
  height: 60px;
  background: white;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  justify-items: center;
  align-items: center; }

.header-left {
  padding-left: 26px; }

.header-small-logo-img {
  width: 30px;
  height: 30px; }

.header-right {
  margin-right: 19px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between; }

.header-nav-wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  justify-items: center;
  align-items: center; }

.nav-item {
  list-style: none;
  text-decoration: none;
  display: inline;
  font-size: 16px;
  line-height: 22px;
  color: #3d4e61;
  margin-right: 30px; }

.header_nav_link {
  font-size: 16px;
  line-height: 22px;
  color: #3d4e61;
  text-decoration: none;
  margin-right: 30px; }

.header-logo {
  width: 30px;
  height: 30px;
  border-radius: 50%; }

.header-profile-wrapper {
  display: flex;
  justify-content: center;
  align-content: center; }

.main-info-block {
  position: relative;
  width: 900px;
  height: 1265px;
  margin: 40px auto 75px; }

.main_photo {
  width: 100%;
  height: 464px;
  object-fit: cover; }

.review {
  display: flex;
  width: 491px;
  flex-direction: row;
  flex-wrap: wrap; }

.review_details {
  display: flex;
  flex-direction: row;
  width: 100%; }

.review_details_photo_wrapper {
  display: flex;
  margin-right: 21px; }

.review_photo {
  width: 50px;
  height: 50px; }

.details_name_date {
  width: 173px;
  height: 28px;
  margin: 2px; }

.details_name {
  font-weight: bolder; }

.details_date {
  font-weight: 500; }

button.contact-host {
  border: 1px solid #118488;
  box-sizing: border-box;
  border-radius: 4px;
  width: 150px;
  height: 50px;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  color: #118488;
  position: absolute;
  left: 0;
  bottom: 0; }

.contact-host {
  width: 176px;
  height: 29px;
  margin-top: 5px; }

.amenities_container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 352px;
  margin-top: 21px;
  margin-bottom: 13px; }

.amenities_item {
  width: 50%;
  height: 29px;
  color: #3d4e61; }

.show-all {
  width: 176px;
  height: 29px;
  margin-bottom: 66px; }

.booking-info-block {
  position: absolute;
  right: 0;
  top: 0;
  width: 330px;
  height: 320px;
  background: white;
  border: 1px solid #e1e7ed;
  box-sizing: border-box;
  border-radius: 3px; }

.booking_input {
  box-sizing: border-box;
  border: 1px solid #e1e7ed;
  padding-left: 17px; }

#date {
  position: absolute;
  left: 30px;
  bottom: 178px; }

#guests {
  position: absolute;
  left: 30px;
  bottom: 112px; }

.booking_button {
  text-align: center;
  background: #fd5c63;
  color: white;
  position: absolute;
  bottom: 38px;
  left: 30px; }

.booking_price {
  position: absolute;
  left: 30px;
  top: 30px;
  width: 112px;
  height: 22px; }

.apartment_type {
  width: 176px;
  height: 29px;
  color: #687c94; }

.apartment-info-parametres {
  margin-top: 15px; }

.apartment-info-parametres > div {
  display: inline;
  height: 29px;
  color: #3d4e61; }

.apartment_location {
  width: 491px;
  height: 120px;
  margin-top: 28px; }

.review_hosted_font {
  font-family: "Roboto", sans-serif;
  margin-top: 14px; }

.host-location {
  color: #3d4e61; }

.host-description {
  color: #3d4e61;
  width: 491px;
  height: 100px;
  margin: 0; }

footer {
  background: white;
  box-shadow: 0 -1px 0 #e1e7ed;
  display: flex; }

.footer-container {
  margin: 44px auto;
  display: grid;
  width: 901px;
  grid-template-columns: repeat(3, auto);
  font-size: 16px;
  line-height: 19px;
  color: #3d4e61; }

.footer-column-title {
  font-weight: bold; }

.footer-item {
  margin-top: 10px; }
